import React, { memo } from 'react';

import { StatusProps } from './types';

import { StatusContainer, StatusText } from './styled';

const Status: React.FC<StatusProps> = ({ status }) => 
  <StatusContainer status={status} >
    <StatusText>
    {status 
      ? '  !  c     15 .'
      : '...  '}
    </StatusText>
  </StatusContainer>;

export default memo(Status);
